body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.rowC {
  display: flex;
  flex-direction: row;
}

.topright {
  float: right;
  font-size: 16px;
  position: relative;
  top: 18px;
  right: 16px;
}

.postcontainer::-webkit-scrollbar {
  display: none;
}

.postcontainer {
  padding: 0px;
  overflow-y: scroll;
  max-height: 430px;
  flex: 1 1;
}

.card {
  background: white;
  border-radius: 2%;
  border-style: solid;
  border-color: #f0f0f0;
  border-width: 2px;
  width: 450px;
  height: 550px;
}

.container {
  padding: 5%;
  padding-top: 0;
  font-family: "Roboto";
  font-weight: normal;
  font-size: 16px;
}

.title {
  font-weight: bold;
  font-size: 20px;
}

.subtitle {
  font-weight: bold;
  color: #b0b0b0;
  font-size: 12px;
}

.row {
  background-color: white;
  padding: 10px 0;
}

.post {
  background: white;
  border-radius: 5%;
  border-style: solid;
  border-color: #f0f0f0;
  border-width: 2px;
  width: 360px;
  padding-left: 20px;
  padding-right: 20px;
  padding-top: 10px;
}

.postbody {
  font-size: 14px;
  color: #b0b0b0;
}

